import React from "react";
import ImageGalleryItem from "./ImageGalleryItem";
import { ImageGalleryItemStyled } from "./ImageGalleryItemStyled";
import { ImageGalleryStyled } from "./ImageGalleryStyled";

const ImageGallery = ({ images }) => {
  return (
    <ImageGalleryStyled>
      {images.map((image) => {
        return (
          <ImageGalleryItem
            key={image.id}
            smallphoto={image.webformatURL}
            bigPhoto={image.largeImageURL}
            tag={image.tags}
          />
        );
      })}
    </ImageGalleryStyled>
  );
};

export default ImageGallery;
