import axios from "axios";
import React, { Component } from "react";
import Button from "./button/Button";
import ImageGallery from "./imagegallery/ImageGallery";
import ImageGalleryItem from "./imagegallery/ImageGalleryItem";

import Modal from "./modal/Modal";
import Searchbar from "./searchbar/Searchbar";

const apiKey = "21696545-9a06c1dceb645ff071558b926";

class App extends Component {
  state = {
    images: [],
    page: 1,
    showModal: false,
  };

  formSubmithandler = ({ name, page }) => {
    axios
      .get(
        `https://pixabay.com/api/?q=${name}&page=${page}&key=${apiKey}&image_type=photo&orientation=horizontal&per_page=12`
      )
      .then(({ data }) =>
        this.setState((prevState) => ({
          images: [...prevState.images, ...data.hits],
        }))
      )
      .catch((error) => console.log(error));
  };

  oneMorePageOnClick = (evt) => {
    console.log(evt.currentTarget);
    const { page, images } = this.state;
    if (evt.currentTarget) {
      this.setState((prevState) => ({
        page: prevState.page + 1,
        images: [...prevState.images, ...images],
      }));
      console.log(this.state);
    }
  };

  render() {
    const { images } = this.state;

    return (
      <>
        {/* <Modal /> */}
        <Searchbar onSubmit={this.formSubmithandler} />
        <ImageGallery images={images}>
          <ImageGalleryItem />
        </ImageGallery>
        <Button oneMorePageOnClick={this.oneMorePageOnClick} />
      </>
    );
  }
}

export default App;
